@import url(https://fonts.googleapis.com/css?family=Reem+Kufi&display=swap);
@import url(https://fonts.googleapis.com/css?family=Scope+One&display=swap);
.header {
    display: grid;
    grid-gap: 3em;
    grid-template-columns: -webkit-min-content auto -webkit-min-content -webkit-min-content -webkit-min-content;
    grid-template-columns: min-content auto min-content min-content min-content;
}
.main {
    height: 98vh;
    display: grid;
    grid-gap: 1em;
    padding-right: 1em;
    padding-left: 1em;
    grid-template-rows: -webkit-min-content auto -webkit-min-content;
    grid-template-rows: min-content auto min-content;
}

h1, h2, a {
    font-family: 'Reem Kufi', sans-serif;
    letter-spacing: 4px;
    color: #414141;
    font-weight: lighter;
    -webkit-text-decoration-line: none;
            text-decoration-line: none;
}

.header-footer {
    font-family: 'Reem Kufi', sans-serif;
    letter-spacing: 4px;
    color: dimgray;
    color: #414141;
    font-weight: lighter;
}

.slim-text {
    font-family: 'Scope One', serif;
    letter-spacing: 0;
}
.footer {
    display: grid;
    grid-gap: 3em;
    grid-template-columns: -webkit-min-content -webkit-min-content;
    grid-template-columns: min-content min-content;
    justify-content: right;
}
.body {
    align-self: center;
}

.grid {
    display: grid;
    grid-template-columns: 60% 40%;
}
.greeting {
    font-size: 13vh;
    letter-spacing: 1ex;
}
